!<template>
  <div id="owner-main-container">
    <el-row style="text-align: left">
      <!-- Menu -->
      <el-col :span='5'>
        <hci-admin-menu :activeIndex='"2-1"'/>
      </el-col>
      <!-- Content -->
      <el-col :offset="5" :span="19">
        <div>
          <div style="text-align: center"><h1>Qun l ch sn</h1></div>
          <div>
            <el-button icon="el-icon-circle-plus" style="margin-left: 1vw;" round @click="transitTo('CreatePitchOwner')">
              Thm mi ch sn
            </el-button>
          </div>
          <div>
            <el-table :data="tableDate">
                <el-table-column prop="username" label="Ti khon" align="center" fixed/>
                <el-table-column prop="ownerName" label="H tn" align="center">
                  <template slot-scope="scope">
                    <el-popover trigger="hover" placement="left">
                      <p>H Tn: {{ scope.row.ownerName }}</p>
                      <p>Email: {{ scope.row.email }}</p>
                      <p>S in thoi: {{ scope.row.phone }}</p>
                      <div slot="reference" class="name-wrapper">
                        <el-tag size="medium">{{ scope.row.ownerName }}</el-tag>
                      </div>
                    </el-popover>
                  </template>
                </el-table-column>
                <el-table-column prop="pitchName" label="Tn Sn" align="center">
                  <template slot-scope="scope">
                    <el-popover trigger="hover" placement="left">
                      <p>Tn Sn: {{ scope.row.pitchName }}</p>
                      <!-- <p>Loi Sn: {{ scope.row.type }}</p> -->
                      <p>a ch: {{ scope.row.address }}</p>
                      <p>Thi gian hot ng: 08:00 ~ 22:00</p>
                      <div slot="reference" class="name-wrapper">
                        <el-tag size="medium">{{ scope.row.pitchName }}</el-tag>
                      </div>
                    </el-popover>
                  </template>
                </el-table-column>
                <!-- <el-table-column prop="address" label="a ch" align="center"/> -->
                <!-- <el-table-column prop="statusString" label="Trng thi" align="center"/> -->
                <el-table-column label="Chc nng" align="center" fixed="right" >
                    <template slot-scope="scope" >
                        <el-button v-if="!scope.row.status" type="text" size="small" @click="updateStatus(scope)">M ti khon</el-button>
                        <el-button v-else type="text" size="small" @click="updateStatus(scope)">ng ti khon</el-button>
                    </template>
                </el-table-column>
            </el-table>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import MenuAdmin from '../Common/MenuAdmin'

export default {
  components: {
    'hci-admin-menu': MenuAdmin
  },
  data () {
    return {
      tableDate: [
        {
          username: 'leminhtuan',
          ownerName: 'L Minh Tun',
          pitchName: 'Sn Phc Lc',
          address: '442 L Vn Vit, Tng Nhn Ph A, Qun 9, TP.HCM',
          type: '5-7-11',
          status: true,
          email: 'tuanlm@gmail.com',
          phone: '0123456789',
          statusString: 'ang hot ng'
        },
        {
          username: 'datvtp',
          ownerName: 'Vn Thnh Phng t',
          pitchName: 'Sn Tin Pht',
          address: '387 Man Thin, Tng Nhn Ph A, Qun 9, TP.HCM',
          type: '5-7-11',
          status: true,
          email: 'datvtp@gmail.com',
          phone: '0123456789',
          statusString: 'ang hot ng'
        },
        {
          username: 'tult',
          ownerName: 'L Thanh T',
          pitchName: 'Sn Hip Ph',
          address: '451/2 L Vn Vit, Tng Nhn Ph A, Qun 9, TP.HCM',
          type: '5-7-11',
          status: true,
          email: 'tult@gmail.com',
          phone: '0123456789',
          statusString: 'ang hot ng'
        },
        {
          username: 'sondt',
          ownerName: 'ng Hong Sn',
          pitchName: 'Sn Kaly',
          address: '315 Hong Diu 2, Linh Trung, Qun Th c, TP.HCM',
          type: '5-7-11',
          status: true,
          email: 'sondt1@gmail.com',
          phone: '0123456789',
          statusString: 'ang hot ng'
        },
        {
          username: 'dunggla',
          ownerName: 'Giang Lu Anh Dng',
          pitchName: 'Sn Nam L',
          address: '55/83B L Vn Vit, Qun 9, TP.HCM',
          type: '5-7-11',
          status: true,
          email: 'dunggla@gmail.com',
          phone: '0123456789',
          statusString: 'ang hot ng'
        }
      ]
    }
  },
  mounted () {
    this.checkAuthen()
  },
  methods: {
    /**
     * Update Status
     */
    updateStatus (scope) {
      let title = (scope.row.status)
        ? 'Hy ti khon'
        : 'M ti khon'

      let message = (scope.row.status)
        ? 'Bn c chc chn mun dng hot ng ca ch sn ny ?'
        : 'Bn c chc chn mun m li ca ch sn ny ?'

      this.$confirm(message, title, {
        confirmButtonText: 'ng ',
        cancelButtonText: 'Hy B',
        type: 'warning'
      }).then(() => {
        const loader = this.getLoader()
        setTimeout(() => {
          this.closeLoader(loader)
          scope.row.status = !scope.row.status
          // scope.row.statusString = (scope.row.status) ? 'ang hot ng' : ' hy'
          this.showMessage('Thao tc thnh cng !', 'success')
        }, 1000)
      })
    },
    /**
     * Show Loader
     */
    getLoader () {
      return this.$loading({
        lock: true,
        spinner: 'el-icon-loading',
        text: 'Loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
    },
    /**
     * Close Loader
     * @param loader
     */
    closeLoader (loader) {
      loader.close()
    },
    /**
     * Transit to another component
     * @param name
     * @param data
     */
    transitTo (name, data) {
      const param = {
        name: name,
        params: data
      }
      this.$router.push(param)
    },
    /**
     * Show default message
     * @param message
     * @param type
     */
    showMessage (message, type) {
      this.$message({
        message: message,
        type: type
      })
    },
    /**
     * Check Authen
     */
    checkAuthen () {
      if (!sessionStorage.getItem('username')) {
        this.transitTo('Login', undefined)
      }
    }
  }
}
</script>
<style>
  #pitch-owner-footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    color: white;
    text-align: center;
  }
</style>



